@font-face {
  font-family: "tc-icons";
  src: url("../assets/fonts/tc-icons.eot?fsfjet");
  src: url("../assets/fonts/tc-icons.eot?fsfjet#iefix")
      format("embedded-opentype"),
    url("../assets/fonts/tc-icons.ttf?fsfjet") format("truetype"),
    url("../assets/fonts/tc-icons.woff?fsfjet") format("woff"),
    url("../assets/fonts/tc-icons.svg?fsfjet#tc-icons") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

@mixin icon {
  font-family: "tc-icons" !important;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  font-size: 24px;
  
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

i {
  @include icon;
}

