.card {
  background-color: $color-white;
  display: flex;
  flex-direction: column;
  width: 100%;
  border: 1px solid;
  padding: 16px;
  width: 100%;
  border-radius: $border-radius-card;

  @each $label, $card-color in $card-colors {
    &-#{$label} {
      border-color: $card-color;
      box-shadow: 8px 8px 0px $card-color;

      &:after {
        content: "";
        width: 4px;
        height: 100%;
        border-radius: 50%;
        border-color: $card-color;
      }
    }
  }
  &-top {
    @include text-xs;
    display: flex;
  }
}

.card-title {
  @include heading-3;
  color: $color-black;
}

.card-label,
.card-date,
.card-location {
  @include text-xs;
  color: $color-grey-70;
}

.card-description {
  @include text-xs;
  color: $color-black;
}
