#landing,
#login {
  display: block;
  position: absolute;
  height: 100vh;
  width: 100vw;
  top: 0;
  right: 0;
  overflow: hidden;
  z-index: 999;
}

#landing {
  background-color: #fafafa;
}

.landing__logo {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.landing__logo svg {
  transition: all 0.3s ease-in-out;
}
.landing__logo svg:hover {
  transform: scale(1.2);
}

#login {
  display: flex;
  flex-direction: column;
  background-color: white;
}
#login .login__logo {
  display: flex;
  height: 8rem;
  margin-top: 2.5rem;
  align-items: center;
  padding-left: 3rem;
}
#login .login__logo img {
  height: 86px;
}
#login .login__form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  margin-top: 2rem;
  padding: 0 29px;
  background-color: #fafafa;
  border-top-left-radius: 36px;
  box-shadow: 0 -2rem 0px #5667d7;
  height: calc(100% - 12.5rem);
}
#login .login__form .topbar,
#login .login__form .form {
  width: 100%;
  max-width: 450px;
  margin: 0 auto;
}
#login .login__form .topbar {
  margin-bottom: 0.5rem;
}
#login .login__form h4 {
  margin-bottom: 2rem;
  color: black;
}
#login .login__form .form__button {
  text-align: right;
}
#login .login__form .form__button .button {
  display: inline-block;
  font-family: "IBM Plex Sans", sans-serif;
  font-style: normal;
  font-size: 12px;
  line-height: 100%;
  font-weight: 500;
  text-rendering: optimizeLegibility;
  padding: 12px 22px;
  background-color: black;
  color: white;
  border: 2px solid black;
  border-radius: 16px 0 16px 0;
  cursor: pointer;
  transition: all 0.3s ease;
}
#login .login__form .form__button .button:hover {
  background-color: white;
  color: black;
}

#dashboard .dashboard__lists {
  display: flex;
  padding: 2rem;
}

/* BUTTONS */
.btn, .btn-secondary, .btn-primary, .btn-primary-add {
  font-family: "IBM Plex Sans", sans-serif;
  font-style: normal;
  font-size: 12px;
  line-height: 100%;
  font-weight: 500;
  text-rendering: optimizeLegibility;
  border: 2px solid;
  border-color: black;
  border-radius: 8px;
  cursor: pointer;
  padding: 8px 16px;
  transition: all 0.5s ease;
  display: inline-flex;
  justify-content: center;
  align-items: center;
}
.btn a, .btn-secondary a, .btn-primary a, .btn-primary-add a {
  text-decoration: none;
  line-height: 20px;
}

.btn-primary, .btn-primary-add {
  border: 2px solid black;
  background-color: black;
  color: white;
}
.btn-primary:hover, .btn-primary-add:hover {
  background-color: white;
  color: black;
}

.btn-primary-add {
  padding-left: 8px;
}
.btn-primary-add:before {
  font-family: "tc-icons" !important;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  font-size: 24px;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  content: "\e907";
  font-size: 20px;
  padding-right: 8px;
  color: white;
}

.btn-home {
  height: 100%;
  background: none;
  padding-left: 8px;
  padding-right: 32px;
  padding: 10px 32px 8px 8px;
  border: 1px solid black;
  border-right: none;
  border-top-left-radius: 8px;
  border-bottom-left-radius: 8px;
  text-decoration: none;
}
.btn-home i {
  color: black;
  font-size: 20px;
}

.btn-secondary {
  background-color: white;
  border-color: black;
  color: black;
}

.btn-edit {
  border: 2px solid;
  border-color: black;
  border-radius: 8px;
}

.btn-edit:hover {
  background: #0e29d8;
  color: #fff;
}

.btn-delete {
  color: var(--secondary-color);
  border: 1px solid var(--secondary-color);
}

.btn-delete:hover {
  background: var(--secondary-color);
  color: #fff;
}

