  font-size: 12px;
  line-height: 16px;
  font-weight: 300;
  text-rendering: optimizeLegibility;
  display: flex;
}

.card-title {
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  color: black;
}

.card-label,
.card-date,
.card-location {
  font-family: "IBM Plex Mono", monospace;
