input[type=text],
input[type=date],
input[type=email],
input[type=password],
textarea,
select {
  height: 48px;
  width: 100%;
  padding: 10px;
  border: 2px solid;
  border-color: #b3b3b3;
  border-radius: 8px;
}

label {
  display: block;
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 16px;
  line-height: 24px;
  font-weight: 300;
  text-rendering: optimizeLegibility;
}

fieldset {
  border: none;
  margin-bottom: 16px;
}
fieldset.radio-btns {
  margin-bottom: 24px;
}
fieldset.radio-btns input[type=radio] {
  opacity: 0;
  position: fixed;
  width: 0;
}
fieldset.radio-btns input[type=radio] + label {
  background-color: white;
  border-color: #b3b3b3;
}
fieldset.radio-btns label {
  display: inline-block;
  background-color: #fafafa;
  padding: 10px 20px;
  font-family: sans-serif, Arial;
  font-size: 16px;
  border: 2px solid #444;
  border-radius: 0;
}
fieldset.radio-btns label.btn-first {
  border-radius: 8px 0 0 8px;
}
fieldset.radio-btns label.btn-last {
  border-radius: 0 8px 8px 0;
}
fieldset.radio-btns input[type=radio]:checked + label {
  background-color: #9ce2a8;
  border-color: #6e6e6e;
}
fieldset.radio-btns input[type=radio]:focus + label {
  border: 2px solid #6e6e6e;
}
fieldset.radio-btns label:hover {
  background-color: #e6e6e6;
}

.form-user-profile {
  max-width: 320px;
}
.form-user-profile label {
  font-family: "IBM Plex Mono", monospace;
  font-size: 12px;
  line-height: 16px;
  font-weight: 300;
  text-rendering: optimizeLegibility;
}
.form-user-profile input {
  height: 48px;
  width: 100%;
  border: 2px solid;
  border-color: #b3b3b3;
  border-radius: 8px;
}

header {
  position: sticky;
  width: 100%;
  bottom: 0;
  grid-area: navbar;
}

.navbar {
  bottom: 0;
  left: 0;
  display: flex;
  background-color: white;
  justify-content: center;
  align-items: top;
  border-top: 1px solid #e6e6e6;
  z-index: 5;
}
.navbar-links {
  display: flex;
  width: 100%;
  justify-content: space-evenly;
}
.navbar-links a {
  position: relative;
  width: 32px;
  padding-top: 16px;
  color: #6e6e6e;
  text-decoration: none;
  text-align: center;
  display: flex;
  justify-content: center;
}
.navbar-links a:hover, .navbar-links a:active {
  color: black;
}
.navbar-links a:hover:before, .navbar-links a:active:before {
  content: "";
  position: absolute;
  top: 0;
  border-top: 2px solid black;
  width: 100%;
}
.navbar-links a i {
  width: 24px;
  height: auto;
}
.navbar-links__text {
  display: none;
}

/* Modal */
.modal-wrapper {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  bottom: 0;
  display: none;
  transition: height 0.5s ease-in-out, opacity, 0.5s;
}
.modal-wrapper .modal {
  position: relative;
  background: #fff;
  width: 100%;
  height: 100%;
  margin: 0;
}
.modal-wrapper .modal-header {
  display: flex;
  align-items: center;
  width: 100%;
  height: 128px;
  padding: 2rem;
  background: #f8fafb;
}
.modal-wrapper .modal-body {
  padding: 2rem;
}

.btn-modal {
  background: var(--primary-color);
  border: none;
  color: white;
  width: 9rem;
}